<template>
    <div class="demo-section">
        <div class="section-header">
            <h2>ğŸ—ºï¸ æ—¶åŒºåœ°å›¾</h2>
            <div class="map-controls">
                <div class="legend">
                    <span class="legend-item">
                        <div class="legend-dot standard"></div>
                        <span>æ ‡å‡†æ—¶é—´</span>
                    </span>
                    <span class="legend-item">
                        <div class="legend-dot dst"></div>
                        <span>å¤ä»¤æ—¶</span>
                    </span>
                    <span class="legend-item">
                        <div class="legend-dot no-dst"></div>
                        <span>å…¨å¹´æ ‡å‡†æ—¶é—´</span>
                    </span>
                </div>
            </div>
        </div>

        <div class="map-container">
            <div ref="mapRef" class="echarts-map"></div>

            <!-- ç‚¹å‡»æç¤ºæ¡† -->
            <div v-if="selectedCity" class="city-tooltip" :style="tooltipStyle">
                <div class="tooltip-header">
                    <span class="flag">{{ selectedCity.flag }}</span>
                    <span class="city-name">{{ selectedCity.name }}</span>
                </div>
                <div class="tooltip-content">
                    <div class="time-info">{{ selectedCity.time }}</div>
                    <div class="timezone-info">{{ selectedCity.offset }}</div>
                    <div class="dst-status" :class="selectedCity.dstClass">
                        {{ selectedCity.dstStatus }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted, reactive } from 'vue'
import { WORLD_CLOCKS, NO_DST_TIMEZONES } from '@/data/constants'
import { formatTime, getTimezoneInfo } from '@/utils/timeUtils'
import worldMapData from '@/data/world.json'

// ECharts ç±»å‹å£°æ˜
declare global {
    interface Window {
        echarts: any
    }
}

interface CityData {
    id: string
    name: string
    flag: string
    coordinates: [number, number]
    time: string
    offset: string
    isDST: boolean
    dstStatus: string
    dstClass: string
}

const mapRef = ref<HTMLElement>()
const selectedCity = ref<CityData | null>(null)
const tooltipStyle = ref<Record<string, string>>({})
let chart: any = null
let timer: number | null = null

const cityData = reactive<CityData[]>([])

// è·å–åŸå¸‚é¢œè‰²
const getCityColor = (city: CityData): string => {
    if (NO_DST_TIMEZONES.includes(WORLD_CLOCKS.find(c => c.id === city.id)?.timezone || '')) {
        return '#9e9e9e' // ç°è‰² - å…¨å¹´æ ‡å‡†æ—¶é—´
    }
    return city.isDST ? '#ffc107' : '#2196f3' // é»„è‰² - å¤ä»¤æ—¶ï¼Œè“è‰² - æ ‡å‡†æ—¶é—´
}

// æ›´æ–°åŸå¸‚æ—¶é—´æ•°æ®
const updateCityData = () => {
    const now = Date.now()

    WORLD_CLOCKS.forEach((city, index) => {
        if (!city.coordinates) return

        const time = formatTime(now, {
            timeZone: city.timezone,
            hour12: false,
            hour: '2-digit',
            minute: '2-digit'
        })

        const timezoneInfo = getTimezoneInfo(city.timezone)

        let dstStatus = 'æ ‡å‡†æ—¶é—´'
        let dstClass = 'standard'

        if (NO_DST_TIMEZONES.includes(city.timezone)) {
            dstStatus = 'å…¨å¹´æ ‡å‡†æ—¶é—´'
            dstClass = 'no-dst'
        } else if (timezoneInfo.isDST) {
            dstStatus = 'å¤ä»¤æ—¶'
            dstClass = 'dst'
        }

        const data: CityData = {
            id: city.id,
            name: city.name,
            flag: city.flag,
            coordinates: city.coordinates,
            time,
            offset: timezoneInfo.offset,
            isDST: timezoneInfo.isDST,
            dstStatus,
            dstClass
        }

        if (cityData[index]) {
            Object.assign(cityData[index], data)
        } else {
            cityData.push(data)
        }
    })

    updateMapData()
}

// æ›´æ–°åœ°å›¾æ•°æ®
const updateMapData = () => {
    if (!chart) return

    const scatterData = cityData.map(city => ({
        name: city.name,
        value: [...city.coordinates, city.time],
        symbol: 'pin',
        symbolSize: [28, 38],
        itemStyle: {
            color: getCityColor(city),
            borderColor: '#fff',
            borderWidth: 2,
            shadowBlur: 4,
            shadowColor: 'rgba(0, 0, 0, 0.3)'
        },
        label: {
            show: true,
            formatter: `{flag|${city.flag}} {time|${city.time}}`,
            position: 'top',
            distance: 18,
            rich: {
                flag: {
                    fontSize: 16,
                    padding: [3, 5]
                },
                time: {
                    fontSize: 12,
                    color: '#333',
                    backgroundColor: 'rgba(255, 255, 255, 0.95)',
                    padding: [4, 8],
                    borderRadius: 4,
                    borderColor: getCityColor(city),
                    borderWidth: 2,
                    shadowBlur: 3,
                    shadowColor: 'rgba(0, 0, 0, 0.2)'
                }
            }
        },
        emphasis: {
            symbolSize: [32, 42],
            itemStyle: {
                shadowBlur: 6,
                shadowColor: 'rgba(0, 0, 0, 0.4)'
            }
        }
    }))

    chart.setOption({
        series: [{
            type: 'scatter',
            coordinateSystem: 'geo',
            data: scatterData
        }]
    })
}

// åˆå§‹åŒ–åœ°å›¾
const initMap = () => {
    if (!mapRef.value) return

    import('echarts').then((echartsModule) => {
        const echarts = (echartsModule as any).default || echartsModule

        chart = echarts.init(mapRef.value!)

        // æ³¨å†Œå…¨é‡ä¸–ç•Œåœ°å›¾æ•°æ®
        echarts.registerMap('world', worldMapData)

        const option = {
            backgroundColor: '#f0f7ff',
            title: {
                text: 'ğŸ—ºï¸ ä¸–ç•Œæ—¶åŒºåœ°å›¾',
                left: 'center',
                top: 15,
                textStyle: {
                    color: '#2c3e50',
                    fontSize: 16,
                    fontWeight: 'bold'
                }
            },
            geo: {
                map: 'world',
                roam: true,
                zoom: 1.1,
                center: [15, 25],
                itemStyle: {
                    areaColor: '#e8f4fd',
                    borderColor: '#b3d9ff',
                    borderWidth: 0.8
                },
                emphasis: {
                    itemStyle: {
                        areaColor: '#cce7f0',
                        borderColor: '#81c4ff'
                    }
                },
                select: {
                    itemStyle: {
                        areaColor: '#a6d4fa'
                    }
                }
            },
            series: [{
                type: 'scatter',
                coordinateSystem: 'geo',
                data: [],
                symbolSize: 20,
                symbol: 'pin',
                emphasis: {
                    symbolSize: 25
                }
            }]
        }

        chart.setOption(option)
        updateCityData()

        // ç›‘å¬ç‚¹å‡»äº‹ä»¶
        chart.on('click', (params: any) => {
            if (params.componentType === 'series') {
                const cityInfo = cityData.find(city => city.name === params.name)
                if (cityInfo) {
                    selectedCity.value = cityInfo
                    tooltipStyle.value = {
                        left: `${params.event.offsetX + 15}px`,
                        top: `${params.event.offsetY - 10}px`,
                        display: 'block'
                    }
                    setTimeout(() => {
                        selectedCity.value = null
                    }, 3000)
                }
            }
        })
    }).catch((error) => {
        console.error('åœ°å›¾åˆå§‹åŒ–å¤±è´¥:', error)
    })
}

onMounted(() => {
    initMap()
    // æ¯åˆ†é’Ÿæ›´æ–°ä¸€æ¬¡æ—¶é—´
    timer = window.setInterval(updateCityData, 60000)
})

onUnmounted(() => {
    if (timer) {
        clearInterval(timer)
    }
    if (chart) {
        chart.dispose()
    }
})
</script>

