<script setup lang="ts">
import { ref, onMounted, onUnmounted } from 'vue'
import type { TimeDisplayData } from '@/types/time'
import { formatTime } from '@/utils/timeUtils'

const timeData = ref<TimeDisplayData>({
  currentTime: '--',
  timestamp: 0,
  utcTime: '--',
  timezone: '--'
})

let timer: number | null = null

const updateTimeDisplay = () => {
  const now = new Date()
  const timestamp = now.getTime()

  timeData.value = {
    currentTime: formatTime(timestamp),
    timestamp,
    utcTime: now.toISOString().replace('T', ' ').substring(0, 19),
    timezone: `UTC${(-now.getTimezoneOffset() / 60) >= 0 ? '+' : ''}${-now.getTimezoneOffset() / 60}`
  }
}

onMounted(() => {
  updateTimeDisplay()
  timer = window.setInterval(updateTimeDisplay, 1000)
})

onUnmounted(() => {
  if (timer) {
    clearInterval(timer)
  }
})
</script>

