<template>
  <div class="demo-section">
    <div class="section-header">
      <h2>ğŸŒ ä¸–ç•Œæ—¶é’Ÿ</h2>
      <div class="timezone-selector">
        <label for="base-timezone">åŸºå‡†æ—¶åŒºï¼š</label>
        <select id="base-timezone" v-model="baseTimezone" @change="updateWorldClocks">
          <option v-for="tz in baseTimezoneOptions" :key="tz.value" :value="tz.value">
            {{ tz.label }}
          </option>
        </select>
      </div>
    </div>

    <div class="world-clock-grid">
      <div
        v-for="clock in worldClocks"
        :key="clock.id"
        class="world-clock-card"
      >
        <div class="card-content">
          <div class="clock-header">
            <div class="city-info">
              <span class="flag">{{ clock.flag }}</span>
              <span class="city-name">{{ clock.name }}</span>
            </div>
            <div class="timezone-name">{{ clock.timezone.split('/')[1]?.replace('_', ' ') }}</div>
          </div>

          <div class="time-section">
            <div class="time-value">{{ clockTimes[clock.id]?.time || '--' }}</div>
            <div class="time-meta">
              <span class="offset-utc">{{ clockTimes[clock.id]?.offset || '--' }}</span>
              <span v-if="clockTimes[clock.id]?.baseOffset" class="offset-base">
                {{ clockTimes[clock.id]?.baseOffset }}
              </span>
              <span v-else-if="clock.timezone === baseTimezone" class="offset-base-reference">
                ğŸ¯ åŸºå‡†æ—¶åŒº
              </span>
            </div>
          </div>
        </div>

        <!-- å¤ä»¤æ—¶è½¬æ¢ä¿¡æ¯æŒ‡ç¤ºå™¨ -->
        <div v-if="clockTimes[clock.id]?.dstTransitionInfo" class="dst-transition-indicator">
          <div class="dst-info-icon" title="ç‚¹å‡»æŸ¥çœ‹å¤ä»¤æ—¶è½¬æ¢ä¿¡æ¯">â„¹ï¸</div>
          <div class="dst-transition-tooltip">
            <div v-if="clockTimes[clock.id]?.dstTransitionInfo?.nextInfo" class="transition-item next-transition">
              <div class="transition-label">ğŸ”œ ä¸‹æ¬¡è½¬æ¢</div>
              <div class="transition-details">{{ clockTimes[clock.id]?.dstTransitionInfo?.nextInfo }}</div>
            </div>
            <div v-if="clockTimes[clock.id]?.dstTransitionInfo?.lastInfo" class="transition-item last-transition">
              <div class="transition-label">ğŸ“‹ ä¸Šæ¬¡è½¬æ¢</div>
              <div class="transition-details">{{ clockTimes[clock.id]?.dstTransitionInfo?.lastInfo }}</div>
            </div>
          </div>
        </div>

        <!-- çŠ¶æ€å¾½ç«  - ç§»åˆ°åº•éƒ¨æ¨ªæ¡ -->
        <div class="status-badge-bottom">
          <span v-if="NO_DST_TIMEZONES.includes(clock.timezone)" class="no-dst-badge bottom-badge">
            ğŸ  å…¨å¹´æ ‡å‡†æ—¶é—´
          </span>
          <span v-else-if="clockTimes[clock.id]?.isDST" class="dst-badge bottom-badge">
            ğŸŒ å¤ä»¤æ—¶
          </span>
          <span v-else class="std-badge bottom-badge">
            â„ï¸ æ ‡å‡†æ—¶é—´
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, onUnmounted } from 'vue'
import type { WorldClock } from '@/types/time'
import { WORLD_CLOCKS, NO_DST_TIMEZONES } from '@/data/constants'
import { formatTime, getTimezoneInfo, getTimezoneOffset, getDSTTransitionInfo, formatDSTTransitionInfo } from '@/utils/timeUtils'

interface ClockTime {
  time: string
  offset: string
  dstStatus: string
  isDST: boolean
  baseOffset?: string
  dstTransitionInfo?: {
    nextInfo: string
    lastInfo: string
  }
}

const worldClocks = ref<WorldClock[]>(WORLD_CLOCKS)
const clockTimes = reactive<Record<string, ClockTime>>({})
const baseTimezone = ref<string>('Asia/Shanghai')

// åŸºå‡†æ—¶åŒºé€‰é¡¹
const baseTimezoneOptions = ref([
  { value: 'Asia/Shanghai', label: 'ğŸ‡¨ğŸ‡³ åŒ—äº¬ (UTC+8)' },
  { value: 'America/New_York', label: 'ğŸ‡ºğŸ‡¸ çº½çº¦ (UTC-5/-4)' },
  { value: 'Europe/London', label: 'ğŸ‡¬ğŸ‡§ ä¼¦æ•¦ (UTC+0/+1)' },
  { value: 'Asia/Tokyo', label: 'ğŸ‡¯ğŸ‡µ ä¸œäº¬ (UTC+9)' },
  { value: 'Australia/Sydney', label: 'ğŸ‡¦ğŸ‡º æ‚‰å°¼ (UTC+10/+11)' },
  { value: 'Europe/Paris', label: 'ğŸ‡«ğŸ‡· å·´é» (UTC+1/+2)' },
  { value: 'America/Los_Angeles', label: 'ğŸ‡ºğŸ‡¸ æ´›æ‰çŸ¶ (UTC-8/-7)' },
  { value: 'UTC', label: 'ğŸŒ UTC (ä¸–ç•Œæ ‡å‡†æ—¶é—´)' }
])

let timer: number | null = null

const updateWorldClocks = () => {
  const now = new Date()
  const timestamp = now.getTime()

  // è·å–åŸºå‡†æ—¶åŒºåç§»
  const baseOffset = getTimezoneOffset(baseTimezone.value)
  const baseTimezoneName = baseTimezoneOptions.value.find(tz => tz.value === baseTimezone.value)?.label.split(' ')[1] || 'åŸºå‡†æ—¶åŒº'

  worldClocks.value.forEach(clock => {
    try {
      // è·å–æŒ‡å®šæ—¶åŒºçš„æ—¶é—´
      const timeInTimezone = formatTime(timestamp, {
        timeZone: clock.timezone,
        hour12: false
      })

      // è·å–æ—¶åŒºä¿¡æ¯
      const timezoneInfo = getTimezoneInfo(clock.timezone)
      const currentOffset = getTimezoneOffset(clock.timezone)

      // è®¡ç®—ä¸åŸºå‡†æ—¶åŒºçš„åç§»
      let baseOffsetText = ''
      if (clock.timezone !== baseTimezone.value) {
        const diffHours = (currentOffset - baseOffset) / 60
        if (diffHours > 0) {
          baseOffsetText = `æ¯”${baseTimezoneName}å¿«${Math.abs(diffHours)}å°æ—¶`
        } else if (diffHours < 0) {
          baseOffsetText = `æ¯”${baseTimezoneName}æ…¢${Math.abs(diffHours)}å°æ—¶`
        } else {
          baseOffsetText = `ä¸${baseTimezoneName}åŒæ­¥`
        }
      }

            // æ ¹æ®æ—¶åŒºæ˜¯å¦ä½¿ç”¨å¤ä»¤æ—¶æ¥è®¾ç½®çŠ¶æ€
      let dstStatus = 'æ ‡å‡†æ—¶é—´'
      let isDST = false
      let dstTransitionInfo = undefined

      if (NO_DST_TIMEZONES.includes(clock.timezone)) {
        dstStatus = 'å…¨å¹´æ ‡å‡†æ—¶é—´'
        isDST = false
      } else {
        isDST = timezoneInfo.isDST
        dstStatus = isDST ? 'å¤ä»¤æ—¶' : 'æ ‡å‡†æ—¶é—´'

        // è·å–å¤ä»¤æ—¶è½¬æ¢ä¿¡æ¯
        const transitionData = getDSTTransitionInfo(clock.timezone)
        if (transitionData.nextTransition || transitionData.lastTransition) {
          dstTransitionInfo = formatDSTTransitionInfo(transitionData, clock.timezone)
        }
      }

      clockTimes[clock.id] = {
        time: timeInTimezone,
        offset: timezoneInfo.offset,
        dstStatus,
        isDST,
        baseOffset: baseOffsetText,
        dstTransitionInfo
      }
    } catch (error) {
      console.error(`æ›´æ–° ${clock.name} æ—¶é—´å¤±è´¥:`, error)
    }
  })
}

// ç§»é™¤äº†getDSTClasså‡½æ•°ï¼Œç°åœ¨åœ¨æ¨¡æ¿ä¸­ç›´æ¥å¤„ç†å¤ä»¤æ—¶çŠ¶æ€æ˜¾ç¤º

onMounted(() => {
  updateWorldClocks()
  timer = window.setInterval(updateWorldClocks, 1000)
})

onUnmounted(() => {
  if (timer) {
    clearInterval(timer)
  }
})
</script>

